,[_c('h2',[_vm._v("Bellavista")]),_c('p',[_vm._v(" 「Bellavista」酒莊創立於1977年。創辦人Vittorio Moretti曾在Gambero Rosso大紅蝦雜誌被評為2008年度最優秀釀造者。酒莊占地200公頃，所以的酒款都以自有園的葡萄釀製， ")])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("Glenfiddich")]),_c('p',[_vm._v(" 1886 年的夏天，在七個兒子與兩個女兒的協助下，威廉•格蘭開始著手實踐他的畢生夢想。他們齊心協力砌上一塊又一塊的石頭，徒手建造蒸餾廠。 "),_c('span',[_vm._v(" 在經過一年的努力後，酒廠終於落成，威廉將其命名為格蘭菲迪，即蓋爾語(Gaelic)中的「鹿之谷」。 ")])])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("Omar")]),_c('p',[_vm._v(" 在台灣唯一不靠海的南投縣，境內超過41座高於3,000米高山，83%面積是高山丘陵。南投酒廠跟員工們有個夢，想造出具台灣風味的威士忌，南投酒廠麥芽威士忌蒸餾場建立於2008年，設備包含3座麥芽貯存槽、1座麥芽粉碎機、4座銅製壺式蒸餾器。 ")])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("Gancia")]),_c('p',[_vm._v(" 崗夏(Gancia)的歷史、價值及傳統儼然已成為義大利慶典及全世界風潮的代名詞。崗夏代表義大利氣泡酒的歷史，是部不僅可閱讀的歷史，同時還可在其歷史酒窖品嚐到真實的感官體驗，這座位於 Canelli 美麗且具歷史性的地下酒窖(Underground Cathedrals)已被聯合國教科文組織(Unesco)認可為世界遺產之一。 ")])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("John Walker")]),_c('p',[_vm._v(" 西元1820年，創辦人John Walker夢想能調和出最優質的蘇格蘭威士忌，因而開始投入蘇格蘭威士忌釀製事業，爾後他以精湛的調和工藝及對品質的要求與堅持，逐步建立起JOHNNIE WALKER蘇格蘭威士忌王朝之不朽傳奇。 ")])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("Boizel")]),_c('p',[_vm._v(" 1834年Auguste和他的妻子在Epernay的Saint Remy大街上創立了寶尚香檳。1853年Auguste夫婦在Epernay的Mont Bernon山坡上修建了酒窖和酒莊，釀製出了屬於這個年輕酒莊的第一款香檳和特級年份香檳，兩大家族開始合作，也促進了寶尚香檳的發展。1920年Jules繼承了寶尚香檳，並繼續擴大國外市場，最遠到達澳洲。他釀製的白中白特釀在當時十分稀有。 ")])])]),_c('li',[_c('div',{staticClass:"item"},[_c('h2',[_vm._v("Macallan")]),_c('p',[_vm._v(" 麥卡倫的酒廠前身是由Alexander Reid所有，1824年取得了當時蘇格蘭少有的釀酒執照，當時就聚集了很多的威士忌蒸餾廠，當時獨具慧眼的Roderick Kemp賣掉了原本位於Skye島的Talisker酒廠後，在1892年接下了Elchies經營幾十年的酒廠，並將酒廠改名為Macallan。 ")])])])])])])])}]

